import { useState, useEffect } from 'react'
import './BettingTimer.scss'

export default function BettingTimer({timeToEnd}) {
  const [days, setDays] = useState(0)
  const [hours, setHours] = useState(0)
  const [minutes, setMinutes] = useState(0)
  const [seconds, setSeconds] = useState(0)

  useEffect(() => {
    const getTimeUntil = () => {
      const deadline = timeToEnd ? parseInt(timeToEnd) * 1000 : 0
      const time = deadline - Date.now()
      if (time < 0) {
        setDays(0)
        setHours(0)
        setMinutes(0)
      } else {
        const seconds = Math.floor((time / 1000) % 60)
        const minutes = Math.floor((time / 1000 / 60) % 60)
        const hours = Math.floor((time / (1000 * 60 * 60)) % 24)
        const days = Math.floor(time / (1000 * 60 * 60 * 24))
        setDays(days)
        setHours(hours)
        setMinutes(minutes)
        setSeconds(seconds)
      }
    }
    const timer = setInterval(getTimeUntil, 1000)

    return () => {
      clearInterval(timer)
    }
  }, [timeToEnd])

  return (
    <div className="betting-timer">
      <div className="timer-container">
        <div className="time-block">
          <div className="time-digits">
            <span className="digit">{minutes < 10 ? 0 : Math.floor(minutes / 10)}</span>
            <span className="digit">{minutes % 10}</span>
          </div>
          <div className="time-label">minutes</div>
        </div>
        
        <div className="time-separator">:</div>
        
        <div className="time-block">
          <div className="time-digits">
            <span className="digit">{seconds < 10 ? 0 : Math.floor(seconds / 10)}</span>
            <span className="digit">{seconds % 10}</span>
          </div>
          <div className="time-label">seconds</div>
        </div>
      </div>
      
      <div className="timer-progress">
        <div 
          className="progress-bar" 
          style={{ 
            width: `${(minutes * 60 + seconds) / (30 * 60) * 100}%` 
          }}
        ></div>
      </div>
    </div>
  )
}
