import { useState, useEffect, useRef, useContext } from "react";
import toast from "react-hot-toast";
import Layout from 'layouts'
import './Betting.scss'
import { Container, Image } from 'react-bootstrap'
import { useMediaQuery } from '@uidotdev/usehooks'
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract } from "wagmi";
import { ethers } from "ethers";
import Coin1Img from 'assets/images/lone-coin.png'
import Coin2Img from 'assets/images/waifunomicscoin.png'
import Coin4Img from 'assets/images/coin4.png'
import Coin5Img from 'assets/images/coin5.png'

import BigTeacherImg from 'assets/images/bigteacher.png'
import BlackArrowImg from 'assets/images/blackarrow.png'
import CoinStackImg from 'assets/images/coinstack.png'
import ConnectWalletButton from 'components/ConnectWalletButton'

import Coin3Img from "assets/images/coin3.png"
import HeartImg from "assets/images/heart.png"

import Combobox from 'components/Combobox'
import { EXPECT_OPTIONS, COIN_OPTIONS } from 'config/betting'
import 'react-tabs/style/react-tabs.scss'
import RoundButton from 'components/RoundButton'
import BettingTimer from "components/BettingTimer";

import {
  TEST_NET,
  GOTCHA_ADDRESS_TEST,
  GOTCHA_ADDRESS_MAIN,
  BETTING_ADDRESS_TEST,
  BETTING_ADDRESS_MAIN,
} from "../config";
import BettingAbi from "../config/abis/BETTING_ABI.json";
import GotchaAbi from "../config/abis/GOTCHA_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

const UINT256_MAX = "115792089237316195423570985008687907853269984665640564039457584007913129639935"

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, fastRefresh: slow }
}

const Betting = () => {
  const tokenInfo = [
    { h: 'HOW TO EARN $GOTCHA', d: 'DEPOSIT $GOTCHA token to BETTING' },
    { h: 'TOKEN BETTING POOL (%)', d: '$1BN (10%)' },
    { h: 'DAILY DISTRIBUTION TO THE NFT POOL', d: '2,739,726 $ GOTCHA DAILY' },
    { h: 'STAKING OPENING', d: 'ONE WEEK FROM THE PRE-SALE DATE' }
  ];

  return (
    <table className="betting-table">
      <tbody>
        {
          tokenInfo.map(info => (
            <tr>
              <th>{info.h}</th>
              <td>{info.d}</td>
            </tr>
          ))
        }
      </tbody>
    </table>
  )
}

export default function BettingPage() {
  const isMobile = useMediaQuery("only screen and (max-width: 768px)");
  const { fastRefresh } = useRefresh();

  const { data: txResult, writeContractAsync } = useWriteContract();

  const { openConnectModal } = useConnectModal();

  const chainId = useChainId();
  const { isConnected, address } = useAccount();
  const { data: balance, refetch: refetchBalance } = useBalance({ address: address });


  ///// Pool Balances //////////////
  const { data: poolEthBalance, refetch: refetchPoolEthBalance } = useBalance({ address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN });

  const { data: poolTokenBalance, refetch: refetchPoolTokenBalance } = useReadContract({
    address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
    abi: GotchaAbi,
    functionName: "balanceOf",
    args: [chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN],
    chainId: chainId
  });

  ///// Betting Contract ///////////////////////////////////
  const { data: currentBetId, refetch: refetchCurrentBetId } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "currentBetId",
    args: [],
    chainId: chainId,
  });

  const { data: ethPrice, refetch: refetchEthPrice } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "getLatestETHPrice",
    args: [],
    chainId: chainId,
  });

  const { data: minEthAmount, refetch: refetchMinEthAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "MIN_BET_ETH_AMOUNT",
    args: [],
    chainId: chainId,
  });

  const { data: maxEthAmount, refetch: refetchMaxEthAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "MAX_BET_ETH_AMOUNT",
    args: [],
    chainId: chainId,
  });

  const { data: minAmount, refetch: refetchMinAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "MIN_BET_AMOUNT",
    args: [],
    chainId: chainId,
  });

  const { data: maxAmount, refetch: refetchMaxAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "MAX_BET_AMOUNT",
    args: [],
    chainId: chainId,
  });

  const { data: earnedEthAmount, refetch: refetchEarnedEthAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "earnedEthAmount",
    args: [isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: lostEthAmount, refetch: refetchLostEthAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "lostEthAmount",
    args: [isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: earnedAmount, refetch: refetchEarnedAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "earnedTokenAmount",
    args: [isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: lostAmount, refetch: refetchLostAmount } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "lostTokenAmount",
    args: [isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: prevBetUserInfo, refetch: refetchPrevBetUserInfo } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "betUserInfo",
    args: [parseInt(currentBetId ? currentBetId : 0) - 1, isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: currentBetUserInfo, refetch: refetchCurrentBetUserInfo } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "betUserInfo",
    args: [parseInt(currentBetId ? currentBetId : 0), isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: prevBetInfo, refetch: refetchPrevBetInfo } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "betInfo",
    args: [parseInt(currentBetId ? currentBetId : 0) - 1],
    chainId: chainId,
  });

  const { data: currentBetInfo, refetch: refetchCurrentBetInfo } = useReadContract({
    address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
    abi: BettingAbi,
    functionName: "betInfo",
    args: [parseInt(currentBetId ? currentBetId : 0)],
    chainId: chainId,
  });

  ////// GotchA Token Contract //////////////////////////////////////////////
  const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
    address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
    abi: GotchaAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : ""],
    chainId: chainId
  });

  const { data: approvedAmount, refetch: refetchApprovedAmount } = useReadContract({
    address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
    abi: GotchaAbi,
    functionName: "allowance",
    args: [address, chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN],
    chainId: chainId
  });

  const regInputForExpect = useRef()
  const regInputForCoin = useRef()
  const [selectedCollection, setSelectedCollection] = useState(EXPECT_OPTIONS[0]);
  const [selectedCoin, setSelectedCoin] = useState(COIN_OPTIONS[0]);
  const [countdownTime, setCountdownTime] = useState(0);
  const [isPreparing, setIsPreparing] = useState(false);
  const [isDeposited, setIsDeposited] = useState(false);

  const [depositAmount, setDepositAmount] = useState(0);
  const [isApproving, setIsApproving] = useState(false);
  const [isDepositing, setIsDepositing] = useState(false);

  useEffect(() => {
    refetchCurrentBetId();
    refetchEthPrice();
    refetchCurrentBetInfo();
    refetchCurrentBetUserInfo();
    refetchEarnedAmount();
    refetchLostAmount();

    refetchApprovedAmount();
  }, [fastRefresh])

  useEffect(() => {
    if (currentBetInfo === undefined || currentBetUserInfo === undefined) return;

    if (currentBetInfo[4]) {
      setIsPreparing(true);
      setCountdownTime(parseInt(currentBetInfo[2]))
    } else {
      setIsPreparing(false);
      setCountdownTime(parseInt(currentBetInfo[3]))
    }

    if (currentBetUserInfo[5])
      setIsDeposited(true);
    else
      setIsDeposited(false);

  }, [currentBetInfo, currentBetUserInfo])

  const getTextBetting = () => {
    if (currentBetUserInfo && currentBetUserInfo[5]) // Already Deposited
      return 'Waiting Result ...';

    if (currentBetInfo && currentBetInfo[5]) // Passed preparing time
      return 'Waiting Result ...';

    if (depositAmount === 0)
      return 'Enter Amount';

    if (isApproving)
      return 'Approving ...';

    if (parseFloat(ethers.formatUnits(tokenBalance ? tokenBalance : "0", 'ether')) < depositAmount)
      return 'Insufficient Token Balance';

    if (selectedCoin.value === 1 && parseFloat(ethers.formatUnits(approvedAmount ? approvedAmount : "0", 'ether')) < depositAmount)
      return 'Approve Token';

    if (isDepositing)
      return 'Depositing ...';


    return "Deposit";
  };

  const handleChange = (e) => {
    setDepositAmount(e.target.value);
  }

  const handleBetting = async () => {
    if (currentBetUserInfo && currentBetUserInfo[5]) // Already Deposited
      return;

    if (currentBetInfo && currentBetInfo[5]) // Passed preparing time
      return;

    if (depositAmount === 0)
      return;

    if (selectedCoin.value === 0) {
      if (depositAmount < parseInt(ethers.formatUnits(minEthAmount ? minEthAmount : "0", 18))) {
        toast.error(`The minimum amount is ${parseInt(ethers.formatUnits(minEthAmount ? minEthAmount : "0", 18))}.`);
        return;
      }

      if (depositAmount > parseInt(ethers.formatUnits(maxEthAmount ? maxEthAmount : "0", 18))) {
        toast.error(`The maximum amount is ${parseInt(ethers.formatUnits(maxEthAmount ? maxEthAmount : "0", 18))}.`);
        return;
      }

      if (parseFloat(ethers.formatUnits(poolEthBalance ? poolEthBalance.value : "0", 'ether')) < depositAmount) {
        toast.error(`$S pool balance is not enough.`);
        return;
      }
    } else {
      if (depositAmount < parseInt(ethers.formatUnits(minAmount ? minAmount : "0", 18))) {
        toast.error(`The minimum amount is ${parseInt(ethers.formatUnits(minAmount ? minAmount : "0", 18))}.`);
        return;
      }

      if (depositAmount > parseInt(ethers.formatUnits(maxAmount ? maxAmount : "0", 18))) {
        toast.error(`The maximum amount is ${parseInt(ethers.formatUnits(maxAmount ? maxAmount : "0", 18))}.`);
        return;
      }

      if (parseFloat(ethers.formatUnits(poolTokenBalance ? poolTokenBalance : "0", 'ether')) < depositAmount) {
        toast.error(`$GG tokne pool balance is not enough.`);
        return;
      }
    }


    if (parseFloat(ethers.formatUnits(tokenBalance ? tokenBalance : "0", 'ether')) < depositAmount)
      return;

    if (selectedCoin.value === 1 && parseFloat(ethers.formatUnits(approvedAmount ? approvedAmount : "0", 'ether')) < depositAmount) {
      setIsApproving(true);

      await writeContractAsync({
        address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
        abi: GotchaAbi,
        functionName: "approve",
        args: [chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN, UINT256_MAX],
      })
        .then(() => {
          setIsApproving(false);
        })
        .catch((err) => {
          console.log(err)
          setIsApproving(false);
        });
      return;
    }

    setIsDepositing(true);

    console.log("handleBetting = ", ethers.parseUnits(depositAmount.toString(), 18), selectedCollection.value === 0 ? true : false)

    await writeContractAsync({
      address: chainId === TEST_NET ? BETTING_ADDRESS_TEST : BETTING_ADDRESS_MAIN,
      abi: BettingAbi,
      functionName: "bet",
      args: [selectedCoin.value === 0 ? true : false, ethers.parseUnits(depositAmount.toString(), 18), selectedCollection.value === 0 ? true : false],
      value: selectedCoin.value === 0 ? ethers.parseUnits(depositAmount.toString(), 18) : 0,
    })
      .then(() => {
        setIsDepositing(false);
      })
      .catch((err) => {
        setIsDepositing(false);
      });
  }

  return (
    <Layout>
      {
        isConnected ?
          <>
            <Container fluid>
              <div className='hero'>
                <h1>betting <span>gotcha</span></h1>
              </div>
              <div className='betting-open-container'>
                <div className="overview">
                  <div className="balance-dashboard">
                    <div className="balance-card main-balance">
                      <div className="card-inner">
                        <div className="balance-icon-wrapper">
                          <div className="balance-icon">
                            <span>$</span>
                          </div>
                        </div>
                        <div className="balance-content">
                          <h3 className="balance-title">Wallet Balance</h3>
                          <div className="balance-amount">
                            <span className="amount-value">{parseInt(ethers.formatUnits(balance ? balance.value : 0, "ether"))}</span>
                          </div>
                          <div className="balance-amount">
                            <span className="token-amount-value">{parseInt(ethers.formatUnits(tokenBalance ? tokenBalance : 0, "ether"))}</span>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="stats-container">
                      <div className="balance-card win-card">
                        <div className="card-inner">
                          <div className="stat-icon-wrapper">
                            <div className="stat-icon win-icon">
                              <span>↑</span>
                            </div>
                          </div>
                          <div className="stat-content">
                            <h3 className="stat-title">Win Amount</h3>
                            <div className="stat-amount">
                              <span className="amount-value">{parseInt(ethers.formatUnits(earnedEthAmount ? earnedEthAmount : 0, "ether"))} S</span>
                            </div>
                            <div className="stat-amount">
                              <span className="amount-value">{parseInt(ethers.formatUnits(earnedAmount ? earnedAmount : 0, "ether"))} GG</span>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="balance-card loss-card">
                        <div className="card-inner">
                          <div className="stat-icon-wrapper">
                            <div className="stat-icon loss-icon">
                              <span>↓</span>
                            </div>
                          </div>
                          <div className="stat-content">
                            <h3 className="stat-title">Loss Amount</h3>
                            <div className="stat-amount">
                              <span className="amount-value">{parseInt(ethers.formatUnits(lostEthAmount ? lostEthAmount : 0, "ether"))} S</span>
                            </div>
                            <div className="stat-amount">
                              <span className="amount-value">{parseInt(ethers.formatUnits(lostAmount ? lostAmount : 0, "ether"))} GG</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <h2 className="betting-section-title">BETTING ROOM</h2>
                <div className="betting-cards-container">
                  <div className="betting-room-card">
                    <div className="card-header">
                      <span className="card-title">BET #{parseInt(currentBetId) - 1}</span>
                      <span className="card-status">{prevBetUserInfo && prevBetUserInfo[5] ? "COMPLETED" : "NOT PARTICIPATED"}</span>
                    </div>
                    <table className="betting-data-table">
                      <tbody>
                        <tr>
                          <td className="label">Entry Price:</td>
                          <td className="value highlight-value">$ {prevBetUserInfo ? (prevBetUserInfo[5] ? ethers.formatUnits(prevBetUserInfo[3], 8) : "--") : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">End Price:</td>
                          <td className="value highlight-value">$ {prevBetInfo ? ethers.formatUnits(prevBetInfo[1], 8) : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">Amount:</td>
                          <td className="value highlight-value">{prevBetUserInfo ? (prevBetUserInfo[5] ? parseFloat(ethers.formatUnits(prevBetUserInfo[2], "ether")) : "--") : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">Expectation:</td>
                          <td className="value highlight-value">{prevBetUserInfo ? (prevBetUserInfo[5] ? (prevBetUserInfo[6] ? "LONG" : "SHORT") : "--") : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">Deposit Coin:</td>
                          <td className="value highlight-value">{prevBetUserInfo ? (prevBetUserInfo[5] ? (prevBetUserInfo[1] ? "S" : "GG") : "--") : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">Result:</td>
                          <td className="value">
                            {(prevBetUserInfo && prevBetUserInfo[5] && parseInt(prevBetUserInfo[7]) === 1) && <span className="result win">WIN</span>}
                            {(prevBetUserInfo && prevBetUserInfo[5] && parseInt(prevBetUserInfo[7]) === 2) && <span className="result lose">LOSE</span>}
                            {(prevBetUserInfo && prevBetUserInfo[5] && parseInt(prevBetUserInfo[7]) === 3) && <span className="result draw">DRAW</span>}
                            {(prevBetUserInfo && !prevBetUserInfo[5]) && "--"}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <div className="betting-entry-card">
                    <div className="card-header">
                      <span className="card-title">BET #{parseInt(currentBetId)}</span>
                      <span className="card-badge">{isDeposited ? "ACTIVE" : "OPEN"}</span>
                    </div>
                    <div className="price-display">
                      <span className="price-label">SONIC Price:</span>
                      <span className="price-value">$ {ethPrice ? ethers.formatUnits(ethPrice, 8) : "--"}</span>
                    </div>
                    <div className="betting-status">
                      {
                        isPreparing ?
                          <div className="status-message deposit">It's time to deposit. You can deposit for</div>
                          :
                          <div className="status-message waiting">Waiting for result.</div>
                      }
                      <BettingTimer timeToEnd={countdownTime} />
                    </div>
                    <table className="betting-data-table">
                      <tbody>
                        <tr>
                          <td className="label">Entry Price:</td>
                          <td className="value highlight-value">$ {currentBetUserInfo ? (currentBetUserInfo[5] ? ethers.formatUnits(currentBetUserInfo[3], 8) : "--") : "--"}</td>
                        </tr>
                        <tr>
                          <td className="label">Expectation:</td>
                          <td className="value">
                            {(isPreparing && !isDeposited) && <Combobox ref={regInputForExpect} options={EXPECT_OPTIONS} value={selectedCollection} onChange={(selectedOption) => setSelectedCollection(selectedOption)} />}
                            {(!isPreparing && !isDeposited) && "--"}
                            {isDeposited && (currentBetUserInfo ? (currentBetUserInfo[6] ? <span className="expectation long">LONG</span> : <span className="expectation short">SHORT</span>) : "--")}
                          </td>
                        </tr>
                        <tr>
                          <td className="label">Deposit Coin:</td>
                          <td className="value">
                            {(isPreparing && !isDeposited) && <Combobox ref={regInputForCoin} options={COIN_OPTIONS} value={selectedCoin} onChange={(selectedOption) => setSelectedCoin(selectedOption)} />}
                            {(!isPreparing && !isDeposited) && "--"}
                            {isDeposited && (currentBetUserInfo ? (currentBetUserInfo[6] ? <span className="expectation long">$S</span> : <span className="expectation short">$GG</span>) : "--")}
                          </td>
                        </tr>
                        <tr>
                          <td className="label">Amount:</td>
                          <td className="value">
                            {(isPreparing && !isDeposited) && (
                              selectedCoin.value === 0 ?
                                <input type="number" placeholder={`${parseInt(ethers.formatUnits(minEthAmount ? minEthAmount : "0", 18))}~${parseInt(ethers.formatUnits(maxEthAmount ? maxEthAmount : "0", 18))}`} className="deposit-input" value={depositAmount} onChange={handleChange} />
                                :
                                <input type="number" placeholder={`${parseInt(ethers.formatUnits(minAmount ? minAmount : "0", 18))}~${parseInt(ethers.formatUnits(maxAmount ? maxAmount : "0", 18))}`} className="deposit-input" value={depositAmount} onChange={handleChange} />
                            )}
                            {(!isPreparing && !isDeposited) && "--"}
                            {isDeposited && <span className="deposit-amount">{parseFloat(currentBetUserInfo ? ethers.formatUnits(currentBetUserInfo[2], "ether") : "--")}</span>}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    <RoundButton
                      key="claim"
                      className="bet-btn pulse-animation"
                      children={getTextBetting()}
                      variant="transparent"
                      onClick={handleBetting}
                    />
                  </div>
                </div>
              </div>
            </Container>
          </>
          :
          <>
            <Container fluid>
              <div className='hero betting-hero'>
                <div className="hero-content">
                  <h1 className="hero-title">Price Prediction <span>Game</span></h1>
                  <div className="hero-subtitle">
                    <div className="token-info">
                      <div className="token-badge">$S</div>
                      <div className="token-future">$GG</div>
                    </div>
                  </div>
                  <div className="connect-section">
                    <button onClick={openConnectModal} className="custom-connect-btn">
                      <div className="btn-content">
                        <div className="btn-icon">
                          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M21 18V19C21 20.1 20.1 21 19 21H5C3.89 21 3 20.1 3 19V5C3 3.9 3.89 3 5 3H19C20.1 3 21 3.9 21 5V6H12C10.89 6 10 6.9 10 8V16C10 17.1 10.89 18 12 18H21ZM12 16H22V8H12V16ZM16 13.5C15.17 13.5 14.5 12.83 14.5 12C14.5 11.17 15.17 10.5 16 10.5C16.83 10.5 17.5 11.17 17.5 12C17.5 12.83 16.83 13.5 16 13.5Z" fill="white" />
                          </svg>
                        </div>
                        <span className="btn-text">Connect Wallet</span>
                      </div>
                      <div className="btn-shine"></div>
                    </button>
                  </div>
                </div>
                <div className="hero-visuals">
                  <div className="price-chart">
                    <div className="chart-line"></div>
                    <div className="prediction-arrows">
                      <div className="prediction up">
                        <span className="arrow">↑</span>
                        <span className="label">LONG</span>
                      </div>
                      <div className="prediction down">
                        <span className="arrow">↓</span>
                        <span className="label">SHORT</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Container>

            <div className="betting-info-section">
              <Container fluid>
                <div className="info-cards">
                  <div className="info-card">
                    <div className="card-icon">
                      <span className="icon">🎮</span>
                    </div>
                    <div className="card-content">
                      <h3>How It Works</h3>
                      <p>Predict whether the $S price will go up (LONG) or down (SHORT) within the betting timeframe. Winners share the prize pool from losing bets.</p>
                    </div>
                  </div>

                  <div className="info-card">
                    <div className="card-icon">
                      <span className="icon">💰</span>
                    </div>
                    <div className="card-content">
                      <h3>Token Evolution</h3>
                      <p>Currently played with $S tokens. After TGE (Token Generation Event), the game will transition to using $GG tokens for all betting activities.</p>
                    </div>
                  </div>

                  <div className="info-card">
                    <div className="card-icon">
                      <span className="icon">⏱️</span>
                    </div>
                    <div className="card-content">
                      <h3>Betting Rounds</h3>
                      <p>Each round lasts for a fixed period. Place your bets before the timer ends. Results are determined by comparing the price at start and end of each round.</p>
                    </div>
                  </div>
                </div>

                <div className="strategy-section">
                  <div className="strategy-content">
                    <h2>Betting Strategy</h2>
                    <p>Analyze market trends and token performance to make informed predictions. The more accurate your predictions, the higher your potential rewards.</p>
                    <ul className="strategy-points">
                      <li><span className="point-highlight">Long Position:</span> Bet on price increase if you believe market conditions are favorable.</li>
                      <li><span className="point-highlight">Short Position:</span> Bet on price decrease if you anticipate market corrections.</li>
                      <li><span className="point-highlight">Risk Management:</span> Only bet what you can afford to lose and diversify your positions.</li>
                    </ul>
                  </div>
                  <div className="strategy-visual">
                    <div className="visual-container">
                      <div className="token-price">
                        <div className="price-display">
                          <span className="currency">$S</span>
                          <span className="value">0.00425</span>
                        </div>
                        <div className="price-trend up">+2.3%</div>
                      </div>
                    </div>
                  </div>
                </div>
              </Container>
            </div>

            <div className="cta-section">
              <Container fluid>
                <div className="cta-content">
                  <h2>Ready to Start?</h2>
                  <p>Connect your wallet now and join the exciting world of price prediction. Test your market analysis skills and earn rewards!</p>
                  <button onClick={openConnectModal} className="custom-connect-btn">
                    <div className="btn-content">
                      <div className="btn-icon">
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M21 18V19C21 20.1 20.1 21 19 21H5C3.89 21 3 20.1 3 19V5C3 3.9 3.89 3 5 3H19C20.1 3 21 3.9 21 5V6H12C10.89 6 10 6.9 10 8V16C10 17.1 10.89 18 12 18H21ZM12 16H22V8H12V16ZM16 13.5C15.17 13.5 14.5 12.83 14.5 12C14.5 11.17 15.17 10.5 16 10.5C16.83 10.5 17.5 11.17 17.5 12C17.5 12.83 16.83 13.5 16 13.5Z" fill="white" />
                        </svg>
                      </div>
                      <span className="btn-text">Connect Wallet</span>
                    </div>
                    <div className="btn-shine"></div>
                  </button>
                </div>
              </Container>
            </div>
          </>
      }
    </Layout>
  )
}
